html {
  font-size: 1rem;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --primary-font:"Noto sans",sans-serif;
  --secondary-font:"Rubik",sans-serif;
  --tertiary-font:"Montserrat",sans-serif;
  --color-white:#fff;
  --color-black: #000;
  --common-color:hsl(260,100%,55%);
  --header-grad-1:hsl(260,100%,40%);
  --header-grad-2:hsl(265,90%,40%);
  --header-grad-3:hsl(270,100%,50%);
  --nav-link-color:rgb(255, 253, 201);
  --h1-color:rgb(61, 64, 77);
  --btn-color:hsl(260, 100%, 50%);
  --del-btn-color:hsl(0, 66%, 45%);
  --legend-color: rgb(73, 75, 84);
  --input-label-color:rgb(82, 80, 94);
  --input-field-border:#bbb;
  --form-link-color:hsl(260, 100%, 50%);
  --shadow-color:rgba(0,0,0,.4);
  --info-msg-bg:hsl(250, 100%, 85%);
  --info-msg-color:hsl(255,90%,50%);
  --error-msg-bg:hsl(4, 100%, 87%);
  --error-msg-color:hsl(5, 100%, 50%);
  --profile-bg-color:hsl(260,100%,5%);
  --profile-card-color:hsl(40,100%,60%);
}

body {
  font: 400 100%/160% var(--primary-font);
  position: relative;
}

.btn {
  font-family: var(--font-tertiary);
  font-size: 0.9rem;
  text-transform: uppercase;
  background-color: var(--btn-color);
  border: none;
  border-radius: 4px;
  font-weight: 400;
  color: var(--color-white);
  letter-spacing: 0.5px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2), 2px 2px 2px var(--shadow-color);
  transition: transform 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28), box-shadow 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
.btn:hover, .btn:focus {
  cursor: pointer;
  transform: translateY(-3px);
  box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.6);
}
.btn:active {
  transform: translateY(-1px);
  box-shadow: 3px 3px 5px var(--shadow-color);
}

.application--header {
  background: linear-gradient(to top left, var(--header-grad-1) var(--x, 0%), var(--header-grad-2), var(--header-grad-3));
  background-size: 200% 200%;
  padding: 0.5rem 1rem;
  animation: header-animate 8000ms ease-in-out infinite;
}

@keyframes header-animate {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 50% 100%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.navigation__bar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
}
.navigation__link {
  display: inline-block;
  padding: 0.25rem;
  text-decoration: none;
  color: var(--nav-link-color);
  font-size: 1.05rem;
}
.navigation__link:hover, .navigation__link:focus {
  text-decoration: underline 1px currentColor;
  text-underline-offset: 3px;
}
.navigation__link:not(.navigation__link:last-child) {
  margin-right: 0.6rem;
}
.navigation__link__active {
  text-decoration: underline 1px currentColor;
  text-underline-offset: 3px;
}

.error__field {
  border-color: #e41919 !important;
}

.field__error__msg {
  font-size: 0.7rem;
  color: #e41919;
  letter-spacing: 0.5px;
  font-weight: 500;
}

.msg__container {
  width: 100%;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  font-family: var(--tertiary-font);
  line-height: 1.25;
}
.msg__container span {
  font-weight: 500;
}
.msg__container button {
  margin-left: 1rem;
  font-weight: 500;
}
.msg__container button:hover, .msg__container button:focus {
  font-weight: 600;
  cursor: pointer;
}

.info__msg__container {
  background-color: var(--info-msg-bg);
}
.info__msg__container span {
  color: var(--info-msg-color);
}
.info__msg__container button {
  background-color: transparent;
  font-size: 1.35rem;
  border: none;
  color: var(--info-msg-color);
}

.error__msg__container {
  background-color: var(--error-msg-bg);
}
.error__msg__container span {
  color: var(--error-msg-color);
}
.error__msg__container button {
  background-color: transparent;
  font-size: 1.35rem;
  border: none;
  color: var(--error-msg-color);
}

@media screen and (max-width: 480px) {
  html {
    font-size: 0.95rem;
  }
}
@media screen and (max-width: 760px) {
  html {
    font-size: 1rem;
  }
}
@media screen and (min-width: 761px) {
  html {
    font-size: 1.1rem;
  }
}